.page-header__nav {
  @media (min-width: $tablet-width) {
    width: 708px;
    margin: 0 auto;
    padding: 24px 0;

    display: grid;
    grid-template-columns: 174px 1fr;
    gap: 16px;
    align-items: center;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
  }
}

.page-header__bar {
  @media (max-width: $mobile-width-only){
    width: 320px;
    margin: 0 auto;
    padding: 10px 20px 18px;

    display: grid;
    grid-template-columns: 34px 1fr 34px;
    gap: 16px;
    align-items: center;
  }
}

.page-header__logo {
  grid-column: 1 / 3;
}

.page-header__toggle {
  @media (min-width: $tablet-width) {
    display: none;
  }
}

.page-header__list {
  margin: 0;
}
